import React, { useContext, useState } from 'react';
import chatContext from '../chatContext';

const Status = ()=>{

    const {user,callStatus} = useContext(chatContext);

    let line;
    switch(callStatus){
        case 'incoming':
            line = ' ';
            break;
        case 'outgoing':
            line = ' '
            break;
        case 'declined':
            line = '  ';
            break;
        case 'accepted':
            line = '  ';
            break;
        case 'hanged up':
            line = '  ';
            break;
        case 'canceled':
            line = '  ';
            break;
        case 'you can\'t call yourself':
            line = '  ';
            break;
        case 'callee with given id not found':
            line = '   id  ';
            break;
        default: 
            line = callStatus;
            break;
    }

    return(
        <div className="status-container">
            <h1>
                 ID: {user.id}
            </h1>
            <h2>
                {line}
            </h2>
        </div>
    );
}

export default Status;